<script>
export default {
  data() {
    return {
      Count: 1,
      hostRate: 97,
      domainRate: 525,
      rentalPeriod: 12,
      totalCost: null
    };
  },
  methods: {
    calculateCost() {
      var self = this;
      if (self.hostRate == 179 || self.hostRate == 358 || self.hostRate == 756 || self.hostRate == 1330 || self.hostRate == 0) {
        self.rentalPeriod = 1;
        self.domainRate = 0;
      } else { }
      self.totalCost = parseInt(self.Count * (parseInt(self.hostRate) + parseInt(self.hostRate * 0.1)) * self.rentalPeriod) + parseInt(self.domainRate);
      self.$store.commit("set_server_cost", self.totalCost);
    }
  }
};
</script>
